import React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Projects = () => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `
  );

  return (
    <Layout>
      <SEO title="projects" />
      <h1>projects</h1>
      <p>projects</p>
    </Layout>
  );
}

export default Projects
